// Заголовки, общие БЄМ блоки и прочее...
@use "vars" as global;

h1.title {
    font-family: Montserrat;
    line-height: 130%;
    font-size: 64px;
    font-weight: 700;

    @media (max-width:global.$tablet) {
        font-size: 48px;
    }

    @media (max-width:global.$mobile) {
        font-size: 32px;
    }

    @media (max-width:global.$mobileSmall) {
        font-size: 24px;
    }
}

h2.title {
    line-height: 130%;
    font-size: 48px;
    font-weight: 700;
     font-family: Montserrat;
}

h3.title {
    line-height: 130%;
    font-size: 32px;
    font-weight: 700;
     font-family: Montserrat;
      margin-bottom: 10px;
}

h4.title {
    line-height: 130%;
    font-size: 24px;
    font-weight: 700;
     font-family: Montserrat;
       margin-bottom: 10px;
}

p.text {
    line-height: 150%;
    opacity: 0.7;

    @media (max-width:global.$mobile) {
        font-size: 14px;
    }
}

a.btn,
button.btn {
    display: flex; 
    justify-content: center;
    align-items: center;
    color: white;
    border: 2px #1f252e solid;
    padding: 10px 12px;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: 600;
    background: #1f252e;
    font-size: 16px;
    transition: all 0.3s ease 0s;

    &:hover {
        border: 2px #1f252e solid;
        background: rgba(255, 255, 255, 0.4);
        color: #1f252e;
        backdrop-filter: blur(5px);
    }

    @media (max-width:global.$mobile) {
        font-size: 14px;
        padding: 6px 8px;
    }
}

a.btn-white,
button.btn-white {
    display: flex; 
    justify-content: center;
    align-items: center;
    color: #1f252e;
    border: 2px white solid;
    padding: 10px 12px;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: 600;
    background: white;
    font-size: 16px;
    transition: all 0.3s ease 0s;

    &:hover {
        border: 2px white solid;
        background: none;
        color: white;
        backdrop-filter: blur(5px);
    }

    @media (max-width:global.$mobile) {
        font-size: 14px;
        padding: 6px 8px;
    }
}
.text.quote {
  margin: 30px 0;
  position: relative;
  opacity: 1;
display: inline-block;
font-family: Playfair Display;
text-align: center;
font-style: italic;
.quote-first {
  position: absolute;
  left: -40px;
  top: -30px;
  width: 40px;
  height: 40px;
}
.quote-second {
  transform: rotate(180deg);
    position: absolute;
  right:  -40px;
  bottom: -30px;
  width: 40px;
  height: 40px;
}
}