$skewDeg: 18deg;
$magicVH: 32.4vh; // this number is magic and depends on skew angle
$scrollTime: 1s;

.skw-page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
transition: all 0.3s ease 0s;
  &__half {
    position: absolute;
    top: 0;
    width: 50%;
    height: 100vh;
    transition: transform $scrollTime;

    &--left {
      left: 0;
      transform: translate3d(-$magicVH, 100%, 0);
        }

    &--right {
      left: 50%;
      transform: translate3d($magicVH, -100%, 0);
    }

    .skw-page.active & {
      transform: translate3d(0,0,0);
    }
  }

  &__skewed {
    overflow: hidden;
    position: absolute;
    top: 0;
    width: 140%;
    height: 100%;
    transform: skewX($skewDeg * -1);
    background: #000;

    .skw-page__half--left & {
      left: -40%;
    }
    .skw-page__half--right & {
      right: -40%;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column wrap;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 0 30%;
    color: #fff;
    transform: skewX($skewDeg);
    transition: transform $scrollTime, opacity $scrollTime;
    background-size: cover;

    .skw-page__half--left & {
      padding-left: 30%;
      padding-right: 30%;
      transform-origin: 100% 0;
    }
    .skw-page__half--right & {
      padding-left: 30%;
      padding-right: 30%;
      transform-origin: 0 100%;
    }

    .skw-page.inactive & {
      opacity: 0.5;
      transform: skewX($skewDeg) scale(0.95);
    }
  }

  &__heading {
    margin-bottom: 15px;
    text-transform: uppercase;
    font-size: 25px;
    text-align: center;
  }

  &__description {
    font-size: 18px;
    text-align: center;
  }
  
  &__link {
    color: #FFA0A0;
  }

  &-1 {
    .skw-page__half--left .skw-page__content {
      background-image: url('https://images.pexels.com/photos/7128303/pexels-photo-7128303.jpeg');
    }
    .skw-page__half--right .skw-page__content {
      background: #101212;
    }
  }
  &-2 {
    .skw-page__half--left .skw-page__content {
      background: #101212;
    }
    .skw-page__half--right .skw-page__content {
      background-image: url('https://i.pinimg.com/736x/52/5a/a5/525aa5585e6f1cba1fb59f2b5638b0ae.jpg');
    }
  }
  &-3 {
    .skw-page__half--left .skw-page__content {
      background-image: url('https://creonline.co.uk/wp-content/uploads/2023/09/4-loss-hope-post.jpg');
      
    }
    .skw-page__half--right .skw-page__content {
      background: #101212;
    }
  }
  &-4 {
    .skw-page__half--left .skw-page__content {
      background: #101212;
    }
    .skw-page__half--right .skw-page__content {
      background-image: url('https://images.unsplash.com/photo-1704202632056-de2c85caf4cf?fm=jpg&q=60&w=3000&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxzZWFyY2h8Mnx8c2lsaG91ZXR0ZSUyMHdvbWFufGVufDB8fDB8fHww');
      background-position: center;
    }
  }
  &-5 {
    .skw-page__half--left .skw-page__content {
      background-image: url('https://lacatholics.org/wp-content/uploads/2020/11/YM-Unmistakable-hope-scaled.jpg');
      background-position: center;
    }
    .skw-page__half--right .skw-page__content {
      background: #101212;
    }
  }
}
