@use "../config/functions" as *;
@use "../config/mixins" as *;
@use "../config/vars" as global;

.header {
	z-index: 50;
	inline-size: 100%;
	transition: background-color var(--bs-transition);

	.header-is-sticky & {
		// ..стили для липкого хедера
		position: fixed;
		background-color: #007aff;
	}

	// .header__container
	&__container {
		display: flex;
		block-size: rem(global.$header-height);
		align-items: center;

		.header-is-sticky & {
			block-size: rem(global.$header-height - 50);
		}
	}
}

.nav-section {
  background:  #101212;
		// .nav__container

		& .nav__container {
     
      & .nav{
        padding: 20px 0;
 display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      gap: 20px;
      flex-wrap: wrap;
       @media (max-width:global.$mobile) {
       gap: 10px;
    }
      & a {
        color: white;
        border: 2px white solid;
        padding: 10px 12px;
        border-radius: 5px;
        text-transform: uppercase;
        font-weight: 600;
        background: #101212;
        transition: all 0.3s ease 0s;
        
   

    @media (max-width:global.$mobile) {
       padding: 6px 8px;
    }

    @media (max-width:global.$mobileSmall) {
    font-size: 14px;
    }
        &:hover{
      
             background: rgba(255, 255, 255, 1);
             color: #101212;
            
        }
      }
      }
		}
}

